body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#nav {
  background-color: #666;
  /* padding: 20px 0; */
}

#nav ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

#nav ul li {
  display: inline-block;
}

#nav ul li a {
  display: block;
  text-decoration: none;
  padding: 16px 10px;
}

#nav ul li a:hover {
  background-color: #ccc;
  color: #999;
}

#paging {
  text-align: center;
}

#paging ul{
  margin: 0;
  padding: 0;
  list-style: none;
}

#paging ul li {
  display: inline-block;
}

#paging ul li a {
  display: block;
  text-decoration: none;
  padding: 16px 10px;
}

#footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#footer ul{
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-block;
}

#footer ul li {
  display: inline-block;
}

#footer ul li a {
  display: block;
  text-decoration: none;
  padding: 16px 10px;
}

.search-excerpt p {
  margin-bottom: 0.9em;
}